import React, { useState, useEffect } from 'react';
import axios from 'axios';
import './ReportsPage.css';

interface Report {
  id: number;
  latitude: number;
  longitude: number;
  address: string;
  description: string;
  createdAt: string;
}

const ReportsPage: React.FC = () => {
  const [reports, setReports] = useState<Report[]>([]);
  const [loading, setLoading] = useState<boolean>(true);

  useEffect(() => {
    const fetchReports = async () => {
      try {
        const response = await axios.get('/reports/');
        setReports(response.data);
      } catch (error) {
        console.error('Error fetching reports:', error);
      } finally {
        setLoading(false);
      }
    };

    fetchReports();
  }, []);

  if (loading) {
    return <div>Loading...</div>;
  }

  return (
    <div className="reports-container">
      <h1>신고 내역</h1>
      <div className="reports-list">
        {/* 예시 신고 내역 */}
        <div className="report-item">
          <strong>과속으로 인한 사고:</strong> 차량이 과속하다가 갑작스러운 제동으로 인해 앞차와 충돌하였습니다. <br/>
          <br/>
          위치: 부산시 해운대구. <br/>
          <small>2024-08-01 10:00</small>
        </div>
        <br/>
        <div className="report-item">
          <strong>골목길 사고:</strong> 좁은 골목길에서 빠르게 진입하던 차량이 튀어나오는 차와 충돌하였습니다. <br/>
          <br/>
          위치: 서울시 종로구. <br/>
          <small>2024-08-03 11:00</small>
        </div>
        <br/>
        <div className="report-item">
          <strong>고속도로 후방 충돌:</strong> 고속도로에서 뒷차가 앞차와의 안전거리를 확보하지 못해 추돌 사고가 발생했습니다. <br/>
          <br/>
          위치: 경기도 성남시. <br/>
          <small>2024-08-04 12:00</small>
        </div>
        <br/>

        {/* 실제 신고 내역 */}
        {reports.map((report) => (
          <div key={report.id} className="report-item">
            <p><strong>위치:</strong> {report.address}</p>
            <p><strong>위도:</strong> {report.latitude}, <strong>경도:</strong> {report.longitude}</p>
            <p><strong>설명:</strong> {report.description}</p>
            <p><strong>신고 일시:</strong> {new Date(report.createdAt).toLocaleString()}</p>
          </div>
        ))}
      </div>
    </div>
  );
};

export default ReportsPage;